.intro-wrapper {
  height: 88vh;
  position: relative;
}

.background-image {
  position: absolute;
  object-fit: cover;
  height: 100%;
  width: 100%;
}

.intro-presentation-wrapper {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(22, 22, 22, 0.5);
  display: flex;
  justify-content: center;
}

.intro {
  height: 80%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.intro-presentation {
  text-align: center;
}
